<script>
import axios from "axios";
import PostList from "@/components/PostList.vue";

export default {
  components: {
    PostList,
  },
  data() {
    return {
      posts: [],
      postLoading: false,
      selectedSort: "",
      searchQuery: "",
      page: 1,
      limit: 60,
      totalPage: 0,
      sortOptions: [
        { value: "name", name: "По названию" },
        { value: "tagline", name: "По сорту" },
        { value: "contributed_by", name: "По создателю" },
      ],
    };
  },
  methods: {
    async fetchBeer() {
      console.log("fetching");
      try {
        this.postLoading = true;
        const res = await axios.get("https://api.punkapi.com/v2/beers", {
          params: {
            page: this.page,
            per_page: this.limit,
          },
        });
        this.posts = res.data;
      } catch (e) {
        console.error(e);
      } finally {
        this.postLoading = false;
      }
    },
  },
  mounted() {
    this.fetchBeer();
  },
  computed: {},
  watch: {},
};
</script>

