<template>
  <div>
    <div v-if="!postLoading">
      <h2>{{ beer.name }}</h2>
      <p>{{ beer.tagline }}</p>
      <p>{{ beer.brewers_tips }}</p>
      <img class="postImg" :src="`${beer.image_url}`" />
    </div>
    <div v-else><p>Loading</p></div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      beer: {},
      id: "",
      postLoading: true,
    };
  },
  methods: {
    getId() {
      const source = window.location.pathname.replace(/[^\d]/g, "");
      this.id = source;
    },
    async fetchExectBeer() {
      try {
        const res = await axios.get(`https://api.punkapi.com/v2/beers/${this.id}`, {});
        this.beer = res.data[0];
      } catch (e) {
        console.error(e);
      } finally {
        this.postLoading = false;
      }
    },
  },
  mounted() {
    this.getId();
    this.fetchExectBeer();
  },
};
</script>

<style scoped>
.postImg {
  max-height: 150px;
}
</style>
